.square-container {
    padding: 5px;
    width: 100px;

    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 100px;
    font-weight: bolder;
    cursor: pointer;
    pointer-events: auto;
    height: 108px;

}

.border-first {
    border-right: 2px solid black;
    border-bottom: 2px solid black;

}

.border-second {
    border-bottom: 2px solid black;
    border-left: 2px solid black;
    border-right: 2px solid black;
}

.border-third {
    border-left: 2px solid black;
    border-bottom: 2px solid black;
}

.border-forth {
    border-top: 2px solid black;
    border-right: 2px solid black;
    border-bottom: 2px solid black;
}

.border-fifth {
    border: 2px solid black;
}

.border-six {
    border-top: 2px solid black;
    border-left: 2px solid black;
    border-bottom: 2px solid black;
}

.border-seven {
    border-top: 2px solid black;
    border-right: 2px solid black;
}

.border-eigth {
    border-right: 2px solid black;
    border-left: 2px solid black;
    border-top: 2px solid black;
}

.border-nine {
    border-left: 2px solid black;
    border-top: 2px solid black;
}

.x-color {
    color: #41bed7;
}

.y-color {
    color: #9f8ab9;
}

p {
    font-size: 6rem;
}

@media only screen and (max-width:500px) {
    .square-container {
        width: 4.3rem;
        height: 4.3rem;
        margin: 0.2px;
    }

    p {
        font-size: 4rem;
    }

}